.programm {
  color: @text-color;
}

.programm__wrapper {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 768px;
    padding: 0 30px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    width: 1300px;
    padding: 0 40px 71px;
  }
}

.programm__list {
  .list-reset();
  display: grid;
  // gap: 20px;
  gap: 24px;

  @media (min-width: @tablet-width) {
    gap: 30px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(2, 570px);
    gap: 20px 80px;
  }
}

.programm__item {
  position: relative;
  padding: 28px 20px 17px;
  background-color: #f2f2f2;

  &--slim::before {
    position: absolute;
    content: "";
    background-image: url("../img/programms/cat-sit.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 50px;
    height: 53px;
    top: 21px;
    left: 20px;

    @media (min-width: @tablet-width) {
      width: 200px;
      height: 207px;
      top: 80px;
      left: 445px;
    }

    @media (min-width: @desktop-width) {
      width: 100px;
      height: 106px;
      top: 41px;
      left: 52px;
    }
  }

  &--pro::before {
    position: absolute;
    content: "";
    background-image: url("../img/programms/cat-stay.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 70px;
    height: 50px;
    top: 24px;
    left: 12px;

    @media (min-width: @tablet-width) {
      width: 268px;
      height: 200px;
      top: 90px;
      left: 410px;
    }

    @media (min-width: @desktop-width) {
      width: 134px;
      height: 100px;
      top: 48px;
      left: 33px;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 75px 300px 60px 52px;
  }

  @media (min-width: @desktop-width) {
    padding: 77px 20px 59px 52px;
  }
}

.programm__title {
  margin: 0 0 30px 70px;
  color: @title-color;
  font-family: @font-title;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-align: left;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin: 0 0 24px 0;
    text-align: left;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 73px 162px;
  }
}

.programm__description {
  .fonts-text-mobile();
  min-width: 227px;
  min-height: 90px;
  margin: 0 0 41px;
  color: @paragraph-color;
  font-family: @font-text;
  font-weight: 400;
  text-align: left;

  @media (min-width: @tablet-width) {
    .fonts-text-tablet();
    width: 340px;
    margin-bottom: 53px;
  }

  @media (min-width: @desktop-width) {
    width: 457px;
    margin-bottom: 10px;
  }
}

.programm__link {
  font-family: @font-title;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-align: left;
  text-transform: uppercase;
  position: relative;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (max-width: @mobile-width-only) {
  .programm__link {
    &::before {
      position: absolute;
      content: "";
      top: -16px;
      width: 240px;
      height: 1px;
      background-color: #d9d9d9;
    }
  }
}

.programm__link::after {
  content: "";
  position: absolute;
  bottom: 6px;
  background-image: url("../img/icon-arrow.svg");
  background-position: -8px;
  background-repeat: no-repeat;
  margin-left: 16px;
  width: 32px;
  height: 12px;
  transition: background-position 0.2s;

  @media (min-width: @tablet-width) {
    margin-left: 25px;
    bottom: 9px;
  }
}

.programm__link:hover::after {
  width: 32px;
  background-position: 0;
}
