@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
body {
  margin: 0 auto;
}
a {
  color: #000000;
  text-decoration: none;
}
img {
  outline-offset: 5px;
  display: block;
  min-width: 100%;
  height: auto;
}
.button {
  padding: 10px 20px;
  width: 100%;
  min-height: 40px;
  background-color: #68B738;
  color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  border: none;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: #ffffff57;
}
