.main-footer__logo-text:hover {
  transition: all 0.3s ease;
  opacity: 0.8;
}
.main-header__logo-text:active,
.main-footer__logo-text:active {
  opacity: 0.6;
}
.main-header__toggle {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  top: 21px;
  right: 21px;
  z-index: 2;
  background-color: #ffffff;
  display: block;
  border: none;
  cursor: pointer;
}
.main-header__toggle:hover,
.main-header__toggle:active {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 2px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-header--closed .main-header__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-header--closed .main-nav__wrapper {
    display: none;
  }
  .main-header--closed .site-list__link {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }
  .main-header--opened .main-nav__wrapper {
    width: 100%;
    position: absolute;
    top: 66px;
    right: 0;
    z-index: 10;
    text-align: center;
    background-color: #ffffff;
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }
  .main-header--nojs .main-header__toggle {
    display: none;
  }
  .main-header--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
    display: block;
  }
}
.main-header--nojs .main-nav {
  border-top: 1px solid #e6e6e6;
}
.main-header--opened .main-header__toggle {
  background-color: #ffffff;
}
.main-header--opened .main-header__toggle::before,
.main-header--opened .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 24px;
  height: 1px;
  background-color: #000000;
}
.main-header--opened .main-header__toggle::before {
  transform: rotate(50grad);
}
.main-header--opened .main-header__toggle::after {
  transform: rotate(-50grad);
  box-shadow: none;
}
