.control-group__label {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 45px;
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .control-group__label {
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    padding-left: 60px;
  }
}
.control-group__mark {
  position: absolute;
  top: -4px;
  left: 0;
  border: 1px solid #cdcdcd;
  width: 30px;
  height: 30px;
}
@media (min-width: 768px) {
  .control-group__mark {
    width: 40px;
    height: 40px;
  }
}
.control-group__input[type="radio"] + .control-group__mark {
  border-radius: 50%;
}
.control-group__input[type="radio"]:checked + .control-group__mark::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 14px;
  top: 7px;
  left: 7px;
  background-color: #68B738;
  border-radius: 50%;
  transition: all 0.2s ease;
}
@media (min-width: 768px) {
  .control-group__input[type="radio"]:checked + .control-group__mark::before {
    top: 10px;
    left: 10px;
    width: 18px;
    height: 18px;
  }
}
.control-group__input[type="checkbox"]:checked + .control-group__mark::before {
  position: absolute;
  content: "";
  background-image: url("../img/icon-tick.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 18px;
  height: 18px;
  top: 7px;
  left: 7px;
  z-index: 1;
}
@media (min-width: 768px) {
  .control-group__input[type="checkbox"]:checked + .control-group__mark::before {
    width: 26px;
    height: 26px;
  }
}
.control-group__label:hover .control-group__mark {
  border-color: #bababa;
}
.control-group__input:focus-visible + .control-group__mark {
  outline: 2px solid #444444;
}
.control-group__input:disabled + .control-group__mark {
  opacity: 0.3;
}
.control-group__input:disabled ~ label {
  opacity: 0.3;
}
